
import { Component, OnInit } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPayrollRekapBulananService } from 'src/app/sdk/payroll/services';
import { Payroll_rekap } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private payrollPayrollRekapService: PayrollPayrollRekapBulananService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }

    listOfData = []
    getData() {
        this.spinner.show()
        this.payrollPayrollRekapService.getPayrollPayrollRekapBulanans(
            JSON.stringify({ rekap: true })
        ).subscribe(
            data => {
                this.listOfData = data.data
                this.spinner.hide()
            }
        )
    }





}
