import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, ViewChild } from '@angular/core';
import { NzTableComponent, NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPayrollRekapBulananService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { DetailPayrollComponent } from '../../../shared/detail-payroll/detail-payroll.component';
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';
import { CompanyDepartemenService } from 'src/app/sdk/hr/services';
import { Departemen } from 'src/app/sdk/hr/models';


const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private payrollPayrollRekapService: PayrollPayrollRekapBulananService,
        private companyDepartemenService: CompanyDepartemenService,
        private pesan: PesanService,
        private notification: NzNotificationService,
        private drawerService: NzDrawerService
    ) { }




    tahun_pajak: string;
    masa_pajak: string;
    pembetulan: string;
    ngOnInit(): void {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('tahun_pajak')) {
                    this.tahun_pajak = data.get('tahun_pajak');
                    this.masa_pajak = data.get('masa_pajak');
                    this.pembetulan = data.get('pembetulan');
                    this.getData()
                }
            }
        )

    }

    departement = []
    getData() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.departement = data.data
                this.departement.forEach((item, index) => {
                    this.departement[index]['rekap'] = []
                    this.departement[index]['list'] = []
                    this.getRekap(index, item.id_departemen);
                })
                console.log(this.departement)
            }
        )
    }

    listData = []
    getRekap(index, id_departement) {
        this.spinner.show()
        let param: any = {
            tahun_pajak: this.tahun_pajak,
            masa_pajak: this.masa_pajak,
            id_departement: id_departement,
            pembetulan: this.pembetulan,
        }
        this.payrollPayrollRekapService.getPayrollPayrollRekapBulanans(JSON.stringify(param)).subscribe(
            data => {
                this.departement[index]['rekap'] = data.data['rekap'];
                this.departement[index]['list'] = data.data['list'];
                this.spinner.hide()
            }
        )
    }


    downloadReportPajak() {
        //download / export ke excl
        this.spinner.show()
        this.payrollPayrollRekapService.getPayrollPayrollRekapBulanans(JSON.stringify({ pajak: true, masa_pajak: this.masa_pajak, tahun_pajak: this.tahun_pajak, pembetulan: this.pembetulan })).subscribe(
            data => {
                let worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(data.data);

                const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };

                const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
                this.saveAsExcelFile(excelBuffer, '1721 bulanan ' + this.masa_pajak + ' ' + this.tahun_pajak);
                this.spinner.hide()
            }
        )
    }



    private saveAsExcelFile(buffer: any, fileName: string): void {
        const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });
        FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + '.xlsx');
    }



    // PREVIEW
    id_pegawai_payroll: string = '';
    detailPerPegawai(id_pegawai_payroll) {
        this.id_pegawai_payroll = id_pegawai_payroll;
        this.openComponent()
    }

    openComponent(): void {
        const drawerRef = this.drawerService.create<DetailPayrollComponent, { value: string }, string>({
            nzTitle: 'Detail Payroll',
            nzContent: DetailPayrollComponent,
            nzWidth: '800px',
            nzContentParams: {
                value: this.id_pegawai_payroll
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.id_pegawai_payroll = data;
            }
        });
    }


}
